<template>
    <ion-page>
      <ion-header mode="ios">
        <ion-toolbar>
          <ion-title>Tambah Soal SNBT</ion-title>
          <ion-back-button slot="start" default-href="/" />
        </ion-toolbar>
      </ion-header>
      <ion-content :fullscreen="true">
        <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
          <ion-refresher-content />
        </ion-refresher>
        <ion-content class="ion-padding">
          <div id="editor-container"></div>
          <ion-item>
            <ion-label>Pilihan A</ion-label>
            <ion-input
              v-model="optionA"
              placeholder="Masukkan pilihan A"
            ></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan B</ion-label>
            <ion-input
              v-model="optionB"
              placeholder="Masukkan pilihan B"
            ></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan C</ion-label>
            <ion-input
              v-model="optionC"
              placeholder="Masukkan pilihan C"
            ></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan D</ion-label>
            <ion-input
              v-model="optionD"
              placeholder="Masukkan pilihan D"
            ></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Pilihan E</ion-label>
            <ion-input
              v-model="optionE"
              placeholder="Masukkan pilihan E"
            ></ion-input>
          </ion-item>
          <ion-item>
            <ion-label>Jawaban Benar</ion-label>
            <ion-select v-model="correctAnswer" placeholder="Pilih Jawaban Benar">
              <ion-select-option value="A">A</ion-select-option>
              <ion-select-option value="B">B</ion-select-option>
              <ion-select-option value="C">C</ion-select-option>
              <ion-select-option value="D">D</ion-select-option>
              <ion-select-option value="E">E</ion-select-option>
            </ion-select>
          </ion-item>
          <ion-button style="color: white" expand="block" @click="addQuestion"
            >Tambah Soal</ion-button
          >
          <ion-grid>
            <ion-row>
              <ion-col>
                <h2>Daftar Soal</h2>
                <ion-list>
                  <ion-item>
                    <ion-label>
                      <div class="table-container">
                        <table>
                          <thead>
                            <tr>
                              <th>Soal</th>
                              <th>Pilihan A</th>
                              <th>Pilihan B</th>
                              <th>Pilihan C</th>
                              <th>Pilihan D</th>
                              <th>Pilihan E</th>
                              <th>Jawaban Benar</th>
  
                              <th>Aksi</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr v-for="question in questions" :key="question.id">
                              <td v-html="question.question"></td>
                              <td>{{ question.options.A }}</td>
                              <td>{{ question.options.B }}</td>
                              <td>{{ question.options.C }}</td>
                              <td>{{ question.options.D }}</td>
                              <td>{{ question.options.E }}</td>
                              <td>{{ question.correctAnswer }}</td>
  
                              <td>
                                <ion-button
                                  @click="editQuestion(question)"
                                  color="warning"
                                  >Edit</ion-button
                                >
                                <ion-button
                                  @click="deleteQuestion(question.id)"
                                  color="danger"
                                  >Hapus</ion-button
                                >
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </ion-label>
                  </ion-item>
                </ion-list>
              </ion-col>
            </ion-row>
          </ion-grid>
        </ion-content>
        <ion-toast
          :is-open="toast.isOpen"
          :message="toast.message"
          :duration="toast.duration"
          :color="toast.color"
          @did-dismiss="() => (toast.isOpen = false)"
        ></ion-toast>
        <ion-modal :is-open="isModalOpen" @did-dismiss="closeModal">
          <ion-header>
            <ion-toolbar>
              <ion-title>Edit Soal</ion-title>
              <ion-buttons slot="end">
                <ion-button @click="closeModal">Close</ion-button>
              </ion-buttons>
            </ion-toolbar>
          </ion-header>
          <ion-content>
            <div id="editor-edit-container"></div>
            <ion-item>
              <ion-label>Pilihan A</ion-label>
              <ion-input
                v-model="editOptionA"
                placeholder="Masukkan pilihan A"
              ></ion-input>
            </ion-item>
            <ion-item>
              <ion-label>Pilihan B</ion-label>
              <ion-input
                v-model="editOptionB"
                placeholder="Masukkan pilihan B"
              ></ion-input>
            </ion-item>
            <ion-item>
              <ion-label>Pilihan C</ion-label>
              <ion-input
                v-model="editOptionC"
                placeholder="Masukkan pilihan C"
              ></ion-input>
            </ion-item>
            <ion-item>
              <ion-label>Pilihan D</ion-label>
              <ion-input
                v-model="editOptionD"
                placeholder="Masukkan pilihan D"
              ></ion-input>
            </ion-item>
            <ion-item>
              <ion-label>Pilihan E</ion-label>
              <ion-input
                v-model="editOptionE"
                placeholder="Masukkan pilihan E"
              ></ion-input>
            </ion-item>
            <ion-item>
              <ion-label>Jawaban Benar</ion-label>
              <ion-select
                v-model="editCorrectAnswer"
                placeholder="Pilih Jawaban Benar"
              >
                <ion-select-option value="A">A</ion-select-option>
                <ion-select-option value="B">B</ion-select-option>
                <ion-select-option value="C">C</ion-select-option>
                <ion-select-option value="D">D</ion-select-option>
                <ion-select-option value="E">E</ion-select-option>
              </ion-select>
            </ion-item>
            <ion-button expand="block" @click="updateQuestion"
              >Simpan Perubahan</ion-button
            >
          </ion-content>
        </ion-modal>
      </ion-content>
    </ion-page>
  </template>
  
  <script setup>
  import {
    IonPage,
    IonHeader,
    IonToolbar,
    IonTitle,
    IonBackButton,
    IonContent,
    IonRefresher,
    IonRefresherContent,
    IonItem,
    IonLabel,
    IonSelect,
    IonSelectOption,
    IonInput,
    IonButton,
    IonToast,
    IonList,
    IonModal,
    IonButtons,
  } from "@ionic/vue";
  import { ref, onMounted } from "vue";
  import {
    getFirestore,
    collection,
    addDoc,
    getDocs,
    updateDoc,
    doc,
    deleteDoc,
  } from "firebase/firestore";
  import {
    getStorage,
    ref as storageRef,
    uploadBytes,
    getDownloadURL,
  } from "firebase/storage";
  import Quill from "quill";
  import "quill/dist/quill.snow.css";
  import { firebaseApp } from "../firebase.js";
  
  const db = getFirestore(firebaseApp);
  const storage = getStorage(firebaseApp);
  
  const subjects = ref([]);
  
  const selectedSubject = ref("");
  const optionA = ref("");
  const optionB = ref("");
  const optionC = ref("");
  const optionD = ref("");
  const optionE = ref("");
  const correctAnswer = ref("");
  const questions = ref([]);
  const isModalOpen = ref(false);
  const editSubject = ref("");
  const editOptionA = ref("");
  const editOptionB = ref("");
  const editOptionC = ref("");
  const editOptionD = ref("");
  const editOptionE = ref("");
  const editCorrectAnswer = ref("");
  const editQuestionId = ref("");
  const toast = ref({
    isOpen: false,
    message: "",
    duration: 2000,
    color: "success",
  });
  let quill;
  let quillEdit;
  
  const fetchQuestions = async () => {
    const querySnapshot = await getDocs(collection(db, "soalsnbt"));
    questions.value = querySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));
  };
  
  const fetchSubjects = async () => {
    const querySnapshot = await getDocs(collection(db, "mataPelajaran"));
    subjects.value = querySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));
  };
  
  const addQuestion = async () => {
    if (
      !optionA.value ||
      !optionB.value ||
      !optionC.value ||
      !optionD.value ||
      !optionE.value ||
      !correctAnswer.value
    ) {
      showToast("Semua kolom harus diisi", "danger");
      return;
    }
  
    const questionText = quill.root.innerHTML;
    if (!questionText) {
      showToast("Soal tidak boleh kosong", "danger");
      return;
    }
  
    try {
      await addDoc(collection(db, "soalsnbt"), {
        subject: selectedSubject.value,
        question: questionText,
        options: {
          A: optionA.value,
          B: optionB.value,
          C: optionC.value,
          D: optionD.value,
          E: optionE.value,
        },
        correctAnswer: correctAnswer.value,
      });
      showToast("Soal berhasil ditambahkan", "success");
      resetForm();
      fetchQuestions(); // Fetch updated questions after adding a new one
    } catch (error) {
      console.error("Error adding question:", error);
      showToast("Terjadi kesalahan saat menambahkan soal", "danger");
    }
  };
  
  const editQuestion = (question) => {
    editQuestionId.value = question.id;
    editSubject.value = question.subject;
    editOptionA.value = question.options.A;
    editOptionB.value = question.options.B;
    editOptionC.value = question.options.C;
    editOptionD.value = question.options.D;
    editOptionE.value = question.options.E;
    editCorrectAnswer.value = question.correctAnswer;
    isModalOpen.value = true;
  
    setTimeout(() => {
      quillEdit = new Quill("#editor-edit-container", {
        theme: "snow",
        modules: {
          toolbar: {
            container: [
              [{ header: [1, 2, false] }],
              ["bold", "italic", "underline"],
              ["image", "video"],
              [{ list: "ordered" }, { list: "bullet" }],
            ],
            handlers: {
              image: () => {
                selectLocalImageForEdit();
              },
              video: () => {
                insertVideoUrlForEdit();
              },
            },
          },
        },
      });
      quillEdit.root.innerHTML = question.question;
    }, 300);
  };
  
  const updateQuestion = async () => {
    // Trim input values to avoid spaces being counted as valid input
    if (
      !editOptionA.value.trim() ||
      !editOptionB.value.trim() ||
      !editOptionC.value.trim() ||
      !editOptionD.value.trim() ||
      !editOptionE.value.trim() ||
      !editCorrectAnswer.value.trim()
    ) {
      showToast("Semua kolom harus diisi", "danger");
      return;
    }
  
    const updatedContent = quillEdit.root.innerHTML.trim();
    if (!updatedContent || updatedContent === "<p><br></p>") {
      showToast("Soal tidak boleh kosong", "danger");
      return;
    }
  
    try {
      await updateDoc(doc(db, "soalsnbt", editQuestionId.value), {
        question: updatedContent,
        options: {
          A: editOptionA.value.trim(),
          B: editOptionB.value.trim(),
          C: editOptionC.value.trim(),
          D: editOptionD.value.trim(),
          E: editOptionE.value.trim(),
        },
        correctAnswer: editCorrectAnswer.value.trim(),
      });
      showToast("Soal berhasil diperbarui", "success");
      isModalOpen.value = false;
      fetchQuestions(); // Fetch updated questions after editing a question
    } catch (error) {
      console.error("Error updating question:", error);
      showToast("Terjadi kesalahan saat memperbarui soal", "danger");
    }
  };
  
  const deleteQuestion = async (questionId) => {
    try {
      await deleteDoc(doc(db, "soalsnbt", questionId));
      showToast("Soal berhasil dihapus", "success");
      fetchQuestions(); // Fetch updated questions after deleting a question
    } catch (error) {
      console.error("Error deleting question:", error);
      showToast("Terjadi kesalahan saat menghapus soal", "danger");
    }
  };
  
  const resetForm = () => {
    selectedSubject.value = "";
    quill.root.innerHTML = ""; // Reset Quill editor content
    optionA.value = "";
    optionB.value = "";
    optionC.value = "";
    optionD.value = "";
    optionE.value = "";
    correctAnswer.value = "";
  };
  
  const showToast = (message, color) => {
    toast.value = {
      isOpen: true,
      message: message,
      duration: 2000,
      color: color,
    };
  };
  
  const doRefresh = (event) => {
    setTimeout(() => {
      fetchQuestions();
      event.target.complete();
    }, 2000);
  };
  
  const closeModal = () => {
    isModalOpen.value = false;
  };
  
  const selectLocalImageForEdit = () => {
    const input = document.createElement("input");
    input.setAttribute("type", "file");
    input.setAttribute("accept", "image/*");
    input.click();
  
    input.onchange = async () => {
      const file = input.files[0];
      const imageRef = storageRef(storage, `images/${file.name}`);
      await uploadBytes(imageRef, file);
      const downloadURL = await getDownloadURL(imageRef);
      const range = quillEdit.getSelection();
      quillEdit.insertEmbed(range.index, "image", downloadURL);
    };
  };
  
  const insertVideoUrlForEdit = () => {
    const videoUrl = prompt("Masukkan URL video (YouTube):");
    if (videoUrl) {
      const range = quillEdit.getSelection();
      const youtubeEmbed = `<iframe width="560" height="315" src="${videoUrl.replace(
        "watch?v=",
        "embed/"
      )}" frameborder="0" allowfullscreen></iframe>`;
      quillEdit.clipboard.dangerouslyPasteHTML(range.index, youtubeEmbed);
    }
  };
  
  onMounted(() => {
    quill = new Quill("#editor-container", {
      theme: "snow",
      modules: {
        toolbar: {
          container: [
            [{ header: [1, 2, false] }],
            ["bold", "italic", "underline"],
            ["image", "video"],
            [{ list: "ordered" }, { list: "bullet" }],
          ],
          handlers: {
            image: () => {
              selectLocalImage();
            },
            video: () => {
              insertVideoUrl();
            },
          },
        },
      },
    });
    fetchQuestions();
    fetchSubjects();
  });
  
  const selectLocalImage = () => {
    const input = document.createElement("input");
    input.setAttribute("type", "file");
    input.setAttribute("accept", "image/*");
    input.click();
  
    input.onchange = async () => {
      const file = input.files[0];
      const imageRef = storageRef(storage, `images/${file.name}`);
      await uploadBytes(imageRef, file);
      const downloadURL = await getDownloadURL(imageRef);
      const range = quill.getSelection();
      quill.insertEmbed(range.index, "image", downloadURL);
    };
  };
  
  const insertVideoUrl = () => {
    const videoUrl = prompt("Masukkan URL video (YouTube):");
    if (videoUrl) {
      const range = quill.getSelection();
      const youtubeEmbed = `<iframe width="560" height="315" src="${videoUrl.replace(
        "watch?v=",
        "embed/"
      )}" frameborder="0" allowfullscreen></iframe>`;
      quill.clipboard.dangerouslyPasteHTML(range.index, youtubeEmbed);
    }
  };
  </script>
  
  <style>
  .table-container {
    overflow-x: auto;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  th,
  td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  th {
    background-color: #f2f2f2;
    font-weight: bold;
  }
  
  tbody tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  tbody tr:hover {
    background-color: #f1f1f1;
  }
  #editor-container,
  #editor-edit-container {
    height: 200px;
  }
  </style>
  